p {
  width: 280px;
  font-family: $SourceSansPro;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.71;
  color: $gray8;
  @include mediaSize($phone) {
    width: auto; } }

.h1 {
  font-family: $Roboto;
  font-size: 3rem;
  font-weight: 300;
  line-height: 1.24;
  @include mediaSize($phone) {
    font-size: 3.6rem; }
  @include mediaSize($tablet) {
    font-size: 4.2rem; } }

.h2 {
  font-size: 3.6rem;
  font-weight: 300;
  line-height: 1.24;
  @include mediaSize($notebook) {
    font-size: 4rem; }
  @include mediaSize($desktop) {
    font-size: 4.2rem; } }

.h3 {
  font-size: 1.6rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 2.4px;
  color: $black2;
  flex-shrink: 0; }

.h4 {
  font-size: 2.1rem;
  font-weight: 400;
  line-height: 1.43;
  color: $black2; }

.h5 {
  font-size: 1.3rem;
  color: $gray2;
  font-weight: 300; }
