.footer-general {
  padding-bottom: 3.7rem;
  &__content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    @include mediaSize($notebook) {
      flex-direction: row; } }
  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include mediaSize($phone) {
      flex-direction: row;
      padding-top: 7.7rem; }
    @include mediaSize($tablet) {
      justify-content: normal; } }
  &__article {
    width: 100%;
    text-align: center;
    @include mediaSize($phone) {
      width: 220px;
      text-align: left; }
    @include mediaSize($tablet) {
      margin: 0 10rem;
      &:last-child {
        margin: 0; } }
    @include mediaSize($notebook) {
      margin: 0; }
    @include mediaSize($desktop) {
      width: 230px; }
    @include mediaSize($projector) {
      width: 271px; } }
  &__title {
    font-size: 1.8rem;
    color: $black2;
    margin-top: 5rem;
    @include mediaSize($phone) {
      margin-top: 0; } }
  &__divider {
    margin: 2.2rem auto 2.1rem;
    @include mediaSize($phone) {
      margin: 2.2rem 0 2.1rem; } }
  &__item {
    padding-bottom: 2.3rem;
    &:last-child {
      padding-bottom: 0; } }
  &__link {
    text-decoration: none;
    display: inline-block;
    &:hover .footer-general__link-txt {
      color: $gray1a; } }
  &__link-txt {
    color: $gray1;
    font-size: 1.3rem;
    line-height: 0; }
  &__txt {
    display: inline-block;
    width: 229px;
    font-size: 1.3rem;
    color: $gray8;
    margin: -0.7rem 0 2.1rem;
    @include mediaSize($phone) {
      display: block; } }
  &__txt-mb12 {
    margin-bottom: 1.2rem; }
  &__contacts {
    display: flex;
    justify-content: center;
    @include mediaSize($phone) {
      justify-content: flex-start; } }

  &__icons {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 118px;
    .fas {
      font-size: 1.8rem;
      color: $gray9;
      vertical-align: middle;
      margin-left: 0; } }
  &__contact-data {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding-left: 1.3rem;
    width: 200px;
    height: 118px;
    overflow: hidden; }
  &__contact-txt {
    color: $gray8;
    font-size: 1.3rem;
    line-height: normal;
    text-align: left;
    &:hover {
      color: $greenLight; } }
  &__posts {
    display: inline-block;
    text-decoration: none;
    margin-bottom: 2.6rem;
    &:last-child {
      margin-bottom: 0; }
    &:hover .footer-general__txt {
      color: $greenLight; } }
  &__instagram {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    @include mediaSize($projector) {
      justify-content: space-between; } }
  &__instagram-photo {
    width: 83px;
    height: 84px;
    background-position: center;
    background-size: cover;
    outline: 0;
    margin-bottom: 0.6rem;
    margin-right: 0.6rem;
    box-sizing: content-box;
    border: 2px solid $white;
    @include mediaSize($phone) {
      display: none;
      &:nth-child(-n+4) {
        display: block; } }
    &:hover {
      border: none;
      width: 87px;
      height: 88px; }
    @include mediaSize($projector) {
      display: block;
      margin-right: 0; } }
  &__follow-instagram {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1.8rem;
    @include mediaSize($phone) {
      justify-content: flex-start; }
    a {
      text-decoration: none;
      color: $greenLight;
      font-size: 1.4rem;
      &:hover {
        text-decoration: underline; } } }
  &__footer-divider {
    display: flex;
    align-items: center;
    margin-top: 5rem;
    @include mediaSize($notebook) {
      margin-top: 0; } }
  &__btn {
    @include mediaSize($notebook) {
      margin-left: -4.5rem; } }
  &__copyright {
    font-size: 1.3rem;
    text-align: center;
    letter-spacing: 0.55;
    opacity: 0.9;
    color: $gray8;
    margin-top: 3.9rem;
    padding-left: 4rem;
    @include mediaSize($phone) {
      padding-left: 0; } } }
