.subscribe {
  padding: 9rem 0;
  background-color: $grayInfo;
  &__new {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
  &__title {
    color: $white;
    text-align: center; }
  &__forms {
    padding-top: 3.3rem; }
  &__form {
    @include mediaSize($notebook) {
      width: 970px; } }
  &__input {
    width: 79%;
    color: $white;
    background-color: $grayInfo;
    border: none;
    border-bottom: 1px solid #a4aaad;
    font-family: $SourceSansPro;
    font-style: italic;
    font-size: 1.4rem;
    padding: 1.9rem 1.1rem 2.2rem;
    margin-left: 3rem;
    outline: 0;
    @include mediaSize($phone) {
      width: 91%; }
    @include mediaSize($notebook) {
      width: 450px; } } }
