.logo-general {
  display: flex;
  align-items: center;
  color: $black2;
  font-size: 1.8rem;
  font-weight: 500;
  text-decoration: none;
  outline: 0; }

.header-general {
  position: relative;
  padding: 3rem 0; }

.nav-general {
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  @include mediaSize($notebook) {
    justify-content: space-between; }
  &__items {
    display: none;
    justify-content: space-between;
    @include mediaSize($tablet) {
      display: flex;
      width: 350px; }
    @include mediaSize($notebook) {
      width: 540px; } }
  &__link {
    font-size: 1.3rem;
    font-weight: 500;
    text-transform: uppercase;
    line-height: .74;
    letter-spacing: 1.45px;
    text-decoration: none;
    color: $gray1;
    &:hover {
      color: $greenLight; }
    @include mediaSize($notebook) {
      letter-spacing: 1.95px; } } }

.search-form {
  background-color: $white3;
  padding: 0 0.5rem 0 2rem;
  border-radius: 1.5rem;
  @include mediaSize($phone) {
    margin-right: 8rem; }
  @include mediaSize($tablet) {
    margin: 0; }
  &__input {
    color: $gray5;
    line-height: 1.78;
    font-weight: 400;
    background-color: $white3;
    border: none;
    border-radius: 40px;
    outline: 0;
    @include mediaSize($phone) {
      font-size: 1.3rem; } }
  &__btn {
    border: none;
    background-color: $white3; } }

.sandwich {
  border: none;
  background-color: $white;
  @include mediaSize($tablet) {
    display: none; } }

// HIDDEN MENU
.nav-general-hidden {
  position: fixed;
  display: none;
  flex-direction: row-reverse;
  justify-content: space-between;
  z-index: 100;
  right: 0;
  top: 0;
  height: 100%;
  width: 100%;
  overflow: auto;
  background-color: transparent;
  &__menu-panel {
    height: 100%;
    padding-top: 1.5rem;
    background-color: $black2; }
  &__items {
    padding: 2rem 3rem; }
  &__item {
    height: 5rem; } }

.close-menu {
  font-size: 1.6rem;
  font-weight: 700;
  color: $greenLight;
  cursor: pointer;
  outline: 0;
  padding-left: 1.5rem; }
