.recent-post {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  border-radius: 3px;
  background-color: $white;
  text-decoration: none;
  outline: 0;
  margin-bottom: 5rem;
  &:last-child {
    margin-bottom: 0; }
  &:hover {
    box-shadow: 0 0 12px 5px $gray3; }
  @include mediaSize($phone) {
    width: 80%; }
  @include mediaSize($tablet) {
    width: 330px;
    &:nth-last-child(-n+2) {
      margin-bottom: 0; } }
  @include mediaSize($desktop) {
    margin-bottom: 0;
    &:last-child {
      display: none; } }
  @include mediaSize($projector) {
    margin-bottom: 0;
    width: 370px; }
  &__img {
    width: 100%;
    height: 230px;
    overflow: hidden;
    border-radius: 3px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; } }
  &__alert {
    display: flex;
    justify-content: center;
    margin-top: -12px; }
  &__content {
    padding-bottom: 5rem;
    text-align: center;
    @include mediaSize($phone) {
      padding: 0 5rem 4rem; } }
  &__title {
    padding: 2.8rem 0 1rem; }
  &__divider {
    margin: 1.8rem auto 1rem; } }

.new-posts {
  display: flex;
  flex-direction: column;
  border-radius: 3px;
  background-color: $white2;
  text-decoration: none;
  outline: 0;
  margin-bottom: 4rem;
  &:last-child {
    margin-bottom: 0; }
  &:hover {
    box-shadow: 0 0 12px 5px $gray3; }
  @include mediaSize($projector) {
    flex-direction: row;
    width: 770px; }
  &__img {
    height: 260px;
    background-position: center;
    background-size: cover;
    @include mediaSize($projector) {
      width: 270px; } }
  &__alert {
    display: flex;
    justify-content: center;
    margin-top: -12px; }
  &__content {
    padding: 5.2rem 1.8rem 3rem;
    @include mediaSize($phone) {
      padding: 5.2rem 2.8rem 3rem; }
    @include mediaSize($projector) {
      padding: 5.2rem 2.8rem 0; } }
  &__title {
    text-align: center;
    @include mediaSize($projector) {
      text-align: left; } }
  &__divider {
    margin: 1.5rem auto;
    @include mediaSize($projector) {
      margin: 1.5rem 0; } }
  &__txt {
    text-align: justify;
    @include mediaSize($projector) {
      width: 408px;
      text-align: left; } }
  &__data {
    padding-top: 2.7rem; } }

.comment {
  display: flex;
  background-color: #f6f6f6;
  margin-bottom: 3rem;
  &:last-child {
    margin-bottom: 0; }
  @include mediaSize($phone) {
    width: 353px; }
  &__img {
    flex-shrink: 0;
    width: 85px;
    height: 85px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; } }
  &__content {
    padding: 0 0 1.2rem 2.1rem;
    text-decoration: none;
    &:hover .comment__txt {
      color: $greenLight; } }
  &__txt {
    font-family: $Roboto;
    font-size: 1.4rem;
    font-weight: 300;
    width: 200px;
    padding-bottom: 1.2rem;
    @include mediaSize($phone) {
      font-size: 1.6rem;
      width: 215px; } } }


.author {
  background-color: $white;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
  border-radius: 5px;
  padding: 2rem 0;
  margin-top: 3rem;
  &:hover {
    box-shadow: 0 0 12px 5px $gray3; }
  @include mediaSize($phone) {
    width: 210px; }
  @include mediaSize($notebook) {
    margin-top: 0; }
  a {
    text-decoration: none;
    &:hover {
      text-decoration: underline; } }
  &__img {
    width: 155px;
    height: 155px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; } }
  &__content {
    padding: 3rem 0.3rem;
    text-align: center; }
  &__position {
    font-size: 1.1rem;
    text-transform: uppercase;
    letter-spacing: 2.2px;
    color: $greenLight;
    padding: 1rem 0; }
  &__icon {
    font-size: 1.3rem;
    color: $gray9;
    &:hover {
      color: $greenLight; } } }
