.video {
  &:hover {
    box-shadow: 0 0 12px 5px $gray3; }
  &__player {
    position: relative;
    height: 202px; }
  &__control {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: $white2;
    height: 3rem; }
  &__progress {
    width: 310px;
    height: 6px;
    border-radius: 3px;
    background-color: $gray2a; } }
