// LOGO
.fa-map-marker-alt {
  color: $greenLight;
  font-size: 1.9rem;
  padding-right: 0.5rem; }

// Search
.fa-search,
.fa-bars {
  color: $black2;
  font-size: 1.6rem;
  cursor: pointer;
  &:hover {
    color: $greenLight; } }

// btn
.fa-long-arrow-alt-left,
.fa-long-arrow-alt-right,
.fa-sync,
.fa-play,
.fa-long-arrow-alt-up {
  font-size: 1.8rem;
  color: $white; }

.social-bar {
  width: 98px;
  display: flex;
  justify-content: space-between; }

.fa-pause {
  font-size: 1.4rem;
  color: $gray1; }

// WHEATHER
.fa-cloud-sun {
  font-size: 4.2rem;
  color: $greenLight; }

// Subscribe
.fa-envelope {
  font-size: 2rem;
  color: $white;
  margin-left: 4.6rem; }

// Instagram
.fa-instagram-square {
  font-size: 1.6rem;
  color: $greenLight;
  margin-right: 1rem; }
