// SECTION
.section {
  display: flex;
  align-items: center;
  &__decor-line {
    background-color: $gray11; }
  &__name {
    padding: 0 2.3rem; } }

// Dividers
.divider {
  width: 42px;
  height: 2px;
  border-radius: 1px; }

.divider_green {
  background-color: $greenLight; }

.divider_white {
  background-color: $white; }

.decor-line {
  width: 100%;
  height: 1px;
  opacity: .3; }

.decor-line-vertical {
  display: none;
  flex-shrink: 0;
  width: 1px;
  height: 417px;
  opacity: .3;
  @include mediaSize($notebook) {
    display: block; } }

// Hide or Show bloks
.active {
  display: block; }

.activeFlex {
  display: flex; }

.hide {
  display: none; }

// COLOR Mark
.active-link {
  color: $gray1a; }

.active-link-hidden-menu {
  color: $greenLight; }

// Btn Slider Adaptive
.for-adaptive {
  display: none;
  @include mediaSize($phone) {
    display: block; } }

// BACKGROUND COLOR
.bck-white {
  background-color: $white;
  &:hover {
    border: 2px solid $white; } }

.bck-blue {
  background-color: $blue;
  &:hover {
    border: 2px solid $blue; } }

// Other
.pl45 {
  @include mediaSize($notebook) {
    padding-left: 4.5rem; } }
