.slider-home {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 10rem;
  background-color: $grayInfo;
  @include mediaSize($phone) {
    padding-top: 20rem; }
  &__decor-line {
    background-color: $white; } }
.slider-general {
  @extend %container;
  justify-content: flex-start;
  @include mediaSize($phone) {
    width: 532px; }
  @include mediaSize($tablet) {
    width: 660px; }
  @include mediaSize($notebook) {
    width: 870px; }
  @include mediaSize($projector) {
    width: 1170px; }
  &__content-position {
    display: none;
    height: 100%;
    &.active {
      display: block; } }
  &__content {
    @include mediaSize($phone) {
      width: 66%; }
    @include mediaSize($tablet) {
      width: 72%; }
    @include mediaSize($notebook) {
      width: 79%; }
    @include mediaSize($projector) {
      width: 84%;
      height: 100%; } }
  &__btn-center {
    display: flex;
    justify-content: center; }
  &__btn-right {
    display: flex;
    justify-content: flex-end; }
  &__title {
    color: $white; }
  &__divider {
    margin: 2.1rem 0 2.7rem; }
  &__txt {
    color: $white;
    font-weight: 300;
    padding-bottom: 2.2rem; } }

.tabs {
  display: flex;
  justify-content: center;
  background-color: $grayInfo;
  padding-top: 4rem;
  @include mediaSize($phone) {
    padding: 21.4rem 0 4.1rem; } }

.slider-general-hidden {
  display: flex;
  justify-content: space-around;
  background-color: $grayInfo;
  padding: 5rem;
  @include mediaSize($phone) {
    display: none; } }

.slider-red {
  color: $white;
  text-align: center;
  &__center {
    margin: 2.1rem auto 2.7rem;
    background-color: $white; } }

.slider-blue {
  color: $white;
  text-align: center;
  &__center {
    margin: 2.1rem auto 2.7rem;
    background-color: $white; } }

.slider-left {
  text-align: right;
  &__divider {
    display: flex;
    justify-content: flex-end; } }
