.btn {
  outline: 0;
  cursor: pointer;
  text-decoration: none;
  border: 1px solid;
  flex-shrink: 0;
  z-index: 10; }

.btn-circle {
  border-radius: 50px; }

.btn-slider {
  width: 7rem;
  height: 7rem;
  border-color: $gray;
  background-color: $grayInfo;
  &:hover {
    background-color: $greenLight; } }

.btn-double {
  display: flex;
  justify-content: space-between;
  border-radius: 2px;
  text-transform: uppercase;
  text-align: center;
  color: $white;
  &__txt {
    width: 156px;
    text-shadow: 0 1px 0 $gray4;
    text-align: center;
    font-size: 1.3rem;
    font-weight: 700;
    line-height: .74;
    letter-spacing: 1.3px;
    padding: 2.1rem 0; }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5.2rem;
    background-color: $greenDark; }
  &__icon-dark {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5.2rem;
    background-color: $transponder;
    border-left: 1px solid $white; } }

.btn-green {
  width: 20.8rem;
  background-color: $greenLight;
  border-color: $greenLight;
  &:hover {
    background-color: $greenDark;
    border-color: $greenDark; } }

.btn-dark {
  background-color: $transponder;
  &:hover {
    background-color: $greenDark;
    border-color: $greenDark; } }

.btn-white {
  background-color: $white3;
  border: 1px solid $gray11;
  opacity: .3;
  .fa-long-arrow-alt-left, .fa-long-arrow-alt-right {
    color: $gray1; }
  &:hover {
    opacity: 1;
    background-color: $greenLight;
    border-color: $greenLight;
    .fa-long-arrow-alt-left, .fa-long-arrow-alt-right {
      color: $white; } } }

.btn-small {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4.7rem;
  height: 4.7rem; }

.btn_green {
  background-color: $greenLight;
  border-color: $greenLight;
  &:hover {
    background-color: $greenDark;
    border-color: $greenDark; } }

.btn-social {
  outline: 0;
  text-decoration: none; }

// TABS
.tab {
  width: 6px;
  height: 6px;
  opacity: .4;
  background-color: $white;
  border-radius: 50%;
  border: 1px solid;
  box-sizing: content-box;
  cursor: pointer;
  margin-right: 1.8rem;
  &:last-child {
    margin-right: 0; }
  &:hover {
    opacity: 1;
    border-color: $white; }
  &.active {
    opacity: 1;
    border-color: $white; } }

.num-tabs {
  display: flex; }

.num-tab {
  font-family: $Roboto;
  outline: 0;
  border: none;
  cursor: pointer;
  background-color: $white3;
  font-size: 1.3rem;
  font-weight: 500;
  color: $gray1;
  &:hover {
    color: $black2; }
  &.active {
    color: $black2; } }

// ASIDE

.tab-aside {
  width: 6px;
  height: 6px;
  background-color: $gray1;
  border-radius: 50%;
  border: 1px solid $white3;
  box-sizing: content-box;
  cursor: pointer;
  margin-right: 1.8rem;
  &:last-child {
    margin-right: 0; }
  &:hover {
    background-color: $greenLight;
    border-color: $greenLight; }
  &.active {
    background-color: $greenLight;
    border-color: $greenLight; } }

// main-social-bar
.main-social-bar {
  @include mediaSize($phone) {
    width: 356px; }
  &__items {
    display: flex;
    justify-content: space-between; }
  &__item {
    width: 98px;
    height: 110px;
    border: 2px solid $white3;
    text-decoration: none;
    text-align: center;
    padding-top: 3rem;
    @include mediaSize($phone) {
      width: 108px; } }
  &__icon {
    font-size: 2.4rem;
    z-index: 10; }
  &__data {
    font-size: 1.4rem;
    font-weight: 300;
    color: $gray1;
    padding-top: 1.5rem; } }

// PLAYER
.btn-play {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  margin: -3rem 0 0 -3rem;
  opacity: 0.8;
  background-color: $greenLight;
  border-color: $greenLight;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  &:hover {
    opacity: 1; } }

.btn-pause {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3.6rem;
  height: 3rem;
  border: none;
  &:hover {
    background-color: $greenLight; }
  &:hover .fa-pause {
    color: $white; } }

// Subscribe
.subscribe-btns {
  display: flex;
  justify-content: center;
  margin-top: 5rem;
  &__btn {
    width: 208px;
    background: linear-gradient(to right,$greenLight 76%,$greenDark 24%);
    border: none;
    border-radius: 2px;
    text-transform: uppercase;
    color: $white;
    text-shadow: 0 1px 0 $gray4;
    font-size: 1.3rem;
    font-weight: 500;
    letter-spacing: 1.3px;
    cursor: pointer;
    padding: 1.6rem 0 1.6rem 2.6rem;
    &:hover {
      background: linear-gradient(to right,$greenDark 76%,$greenLight 24%); } } }
