.weather {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100px;
  background-color: $white2;
  padding-right: 3.1rem;
  text-decoration: none;
  outline: 0;
  &:hover {
    box-shadow: 0 0 12px 5px $gray3; }
  &__data {
    font-size: 5.5rem;
    font-weight: 300;
    line-height: 0.73;
    color: $gray1a; }
  &__city {
    font-size: 1.6rem;
    color: $gray1a;
    padding-bottom: 1.4rem; } }
