.recent-posts {
  padding: 8.8rem 0 7.5rem;
  background-color: $white3;
  @include mediaSize($projector) {
    background: linear-gradient($white 47%,$white3 53%); }
  &__content {
    padding: 4.5rem 0 3rem;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    @include mediaSize($projector) {
      justify-content: space-between; } }
  &__update {
    display: flex;
    align-items: center; } }
