.main-contents {
  padding-bottom: 8rem;
  background-color: $white3; }

.main-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  @include mediaSize($tablet) {
    flex-direction: row;
    align-items: flex-start; }
  &__general {
    @include mediaSize($tablet) {
      width: 50%; }
    @include mediaSize($notebook) {
      width: 60%; }
    @include mediaSize($projector) {
      width: 770px; } }
  &__news {
    display: none; }
  &__control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 1.5rem;
    @include mediaSize($tablet) {
      padding-bottom: 0; } }
  &__decor-line {
    background-color: $gray11; } }

.main-aside {
  width: 100%;
  @include mediaSize($phone) {
    width: 356px; }
  @include mediaSize($tablet) {
    display: none; }
  &.active {
    display: block; }
  &__header {
    display: flex;
    align-items: center;
    padding: 6rem 0 3.2rem; }
  &__decor-line {
    background-color: $gray11;
    margin-left: 2rem; }
  &__body-advertisement {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
  &__item-advertisement {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 170px;
    text-decoration: none;
    background-color: $gray2a;
    font-size: 1.4rem;
    font-weight: 300;
    color: $gray1;
    padding-top: 1.5rem;
    margin-bottom: 1.6rem;
    &:nth-last-child(-n+1) {
      margin-bottom: 0; }
    &:hover {
      font-size: 2rem; }
    @include mediaSize($phone) {
      width: 170px;
      &:nth-last-child(-n+2) {
        margin-bottom: 0; } } }
  &__body-tweets {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include mediaSize($phone) {
      flex-direction: row; } }
  &__tweet {
    height: 147px;
    position: relative;
    overflow: hidden;
    @include mediaSize($phone) {
      width: 322px; } }
  &__content {
    background-color: $white;
    margin-top: -1.5rem;
    padding: 2.3rem 0 1.9rem 2.8rem;
    a {
      text-decoration: none;
      color: $greenLight;
      font-weight: 400;
      &:hover {
        text-decoration: underline; } }
    .h5 {
      margin-top: 2rem; }
    @include mediaSize($phone) {
      margin-top: 0;
      padding: 2.3rem 3.8rem 1.9rem 2.8rem; } }
  &__txt {
    width: 275px;
    @include mediaSize($phone) {
      width: 280px; } }
  &__tabs {
    display: flex;
    justify-content: center;
    margin-top: 2.5rem; } }

.content-top {
  &__slide {
    height: 503px;
    background-position: center;
    background-size: cover;
    padding: 12rem 0 0 4rem;
    margin-bottom: 4rem;
    @include mediaSize($phone) {
      padding: 23rem 2.6rem 4.2rem 4rem; }
    @include mediaSize($tablet) {
      padding: 16rem 2.6rem 4.2rem 4rem; }
    @include mediaSize($projector) {
      width: 770px;
      padding: 22rem 15.6rem 4.2rem 4rem; } }
  &__title {
    color: $white; }
  &__divider {
    margin: 1rem 0 2rem; }
  &__txt {
    color: $white;
    font-weight: 300; }
  &__btn {
    display: flex;
    padding-top: 2.7rem; } }
